import React from 'react'
import { Link } from 'react-router-dom'

const Header = ({location}) => (
    <div
    className="header">
    <div
      className="subheader">
      <div
        className="logo">
      </div>
      <ul>
        <nav>
          <li>
            <Link 
              to="/"
              className={location.pathname === '/' || location.pathname.startsWith('/article/')? 'active' : ''}>Новини</Link>
          </li>
          <li>
            <Link 
              to="/team"
              className={location.pathname === '/team'? 'active' : ''}>Състав</Link>
          </li>
          <li>
            <Link 
              to="/schedule"
              className={location.pathname === '/schedule'? 'active' : ''}>Мачове</Link>
          </li>
          <li>
            <Link to="/">Клуб</Link>
          </li>
          <li>
            <Link to="/">Фен зона</Link>
          </li>
        </nav>
      </ul>
    </div>
  </div>
)

export default Header