import React, { Component } from 'react'

const ScheduleData = [
    "13-10-2018 16:00",
    "ФК Ботев Дебелец - ФК\"Ботев\" с. Горско Ново Село",
    "20-10-2018 16:00",
    "ФК\"Победа\" с.Кесарево - ФК Ботев Дебелец",
    "27-10-2018 15:00",
    "ФК Ботев Дебелец - ФК\"Росица\" с.Поликраище",
    "03-11-2018 14:30",
    "ФК\"Спортист\" с.Козаревец - ФК Ботев Дебелец",
    " 10-11-201  14:30",
    "ФК Ботев Дебелец - ФК\"Първомайци\" с.Първомайци",
    " 17-11-201  14:30",
    "ФК\"Долна Оряховица\" гр. Долна Оряхивица - ФК Ботев Дебелец",
    " 24-11-201  14:30",
    "ФК Ботев Дебелец - ФК\"Вихър\" с.Добри Дял",
    " 01-12-201  14:30",
    "ФК\"Олимпиакос\" с. Никюп - ФК Ботев Дебелец"
];

export default class Schedule extends Component {
    render () {
        return (<div>
            <h3 className="teamTitle">
            Програма
        </h3>
        <div className="schedule">
            {ScheduleData.map((item, i) => {
                return (<div key={i}>
                    {i % 2 === 0? item.split('-')[0] + "." + item.split('-')[1] + ", " + item.substr(-5): item}
                    </div>)
            })}
        </div>
        </div>)
    }
}