import React, { Component } from 'react'
import { tryToLogin, fetchAllAdminNews, createNews, deleteNews } from "./../api/request";

export default class CMSPage extends Component {

    constructor(props) {
        super(props)

        this.state = {
            news: [],
            news_title: "",
            news_content: "",
            news_picture: "",
            login_username: "",
            login_password: "",
            logged: false
        }

        this.handleLogin = this.handleLogin.bind(this)
        this.handleLoginProceed = this.handleLoginProceed.bind(this)
        this.handleChangeInput = this.handleChangeInput.bind(this)
        this.handleSubmitNews = this.handleSubmitNews.bind(this)
        this.handleProceedNews = this.handleProceedNews.bind(this)
        this.handleDeleteNews = this.handleDeleteNews.bind(this)
    }

    handleChangeInput(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    handleSubmitNews(e) {
        e.preventDefault()
    }

    handleLogin(e) {
        e.preventDefault()
    }

    handleProceedNews() {

        const data = new FormData()

        data.append('title', this.state.news_title)
        data.append('content', this.state.news_content)
        data.append('file', this.uploadInput.files[0])

        createNews(data)
        .then(() => {
            this.setState({
                news_title: "",
                news_content: "",
                news_picture: ""
            })
            fetchAllAdminNews()
            .then(response => response.json())
            .then(allNews => {
                this.setState({
                    news: allNews
                })
            })
        })
    }

    handleDeleteNews(e) {

        e.preventDefault()

        deleteNews(e.target.id)
        .then(() => {
            fetchAllAdminNews()
            .then(response => response.json())
            .then(allNews => {
                this.setState({
                    news: allNews
                })
            })
        })
    }

    handleLoginProceed() {

        const loginData = new FormData()

        loginData.append('username', this.state.login_username)
        loginData.append('password', this.state.login_password)

        tryToLogin(loginData)
        .then(response => response.json())
        .then(answer => {
            if (answer.status === 'ok') {
                this.setState({
                    logged: true
                })
            }
        })
    }

    componentDidMount() {
        fetchAllAdminNews()
        .then(response => response.json())
        .then(allNews => {
            this.setState({
                news: allNews
            })
        })
    }

    componentWillUnmount() {
        this.setState({
            logged: false
        })
    }

    render() {
        let renderIfLogged = (<div>
            <h1>Управление на съдържанието</h1>
            <h3>Създай новина</h3>
            <form
                onSubmit={this.handleSubmitNews}>
                <input 
                    type="text" 
                    name="news_title" 
                    value={this.state.news_title}
                    placeholder="Заглавие" 
                    onChange={this.handleChangeInput}/>
                <br />
                <textarea 
                    name="news_content"
                    value={this.state.news_content}
                    onChange={this.handleChangeInput}>
                </textarea>
                <br />
                <input ref={(ref) => { this.uploadInput = ref; }} type="file" />
                <br />
                <button 
                    type="submit"
                    onClick={this.handleProceedNews}>
                    Запази
                </button>
            </form>
            <br />
            <h3>Изтрий новина</h3>
            {this.state.news.map((item, i) => {
                return (<div key={i}>
                    {item.title}
                    <button
                        id={item._id}
                        onClick={this.handleDeleteNews}
                    >
                        Изтрий
                    </button>
                </div>)
            })}
          </div>)
        let renderIfGuest = (<div>
            <h1>Вход</h1>
            <form onSubmit={this.handleLogin}>
                <input 
                    type="text" 
                    name="login_username" 
                    value={this.state.login_username}
                    placeholder="Потребител" 
                    onChange={this.handleChangeInput}/>
                <br />
                <input 
                    type="password" 
                    name="login_password" 
                    value={this.state.login_password}
                    placeholder="Парола" 
                    onChange={this.handleChangeInput}/>
                <br />
                <button 
                    type="submit"
                    onClick={this.handleLoginProceed}>
                    Влез
                </button>
            </form>
        </div>)
        return (<div>
            {this.state.logged === true? renderIfLogged : renderIfGuest}
        </div>)
    }
}