import React, { Component } from 'react'
import { fetchRanking } from './../api/request'

export default class Standing extends Component {
  constructor(props) {
    super(props)

    this.state = {
        ranking: '',
        roman_ranking_number: ''
    }
  }

  componentDidMount() {
      fetchRanking()
      .then(response => response.json())
      .then(dataRanking => {
          this.setState({
              ranking: dataRanking
          }, () => {
            switch (dataRanking.games_played) {
              case "1":
                this.setState({ roman_ranking_number: 'I' })
                break
              case "2":
                this.setState({ roman_ranking_number: 'II' })
                break
              case "3":
                this.setState({ roman_ranking_number: 'III' })
                break
              case "4":
                this.setState({ roman_ranking_number: 'IV' })
                break
              case "5":
                this.setState({ roman_ranking_number: 'V' })
                break
              case "6":
                this.setState({ roman_ranking_number: 'VI' })
                break
              case "7":
                this.setState({ roman_ranking_number: 'VII' })
                break
              case "8":
                this.setState({ roman_ranking_number: 'VIII' })
                break
              case "9":
                this.setState({ roman_ranking_number: 'IX' })
                break
              case "10":
                this.setState({ roman_ranking_number: 'X' })
                break
              case "11":
                this.setState({ roman_ranking_number: 'XI' })
                break
              case "12":
                this.setState({ roman_ranking_number: 'XII' })
                break
              case "13":
                this.setState({ roman_ranking_number: 'XIII' })
                break
              case "14":
                this.setState({ roman_ranking_number: 'XIV' })
                break
              case "15":
                this.setState({ roman_ranking_number: 'XV' })
                break
              case "16":
                this.setState({ roman_ranking_number: 'XVI' })
                break
              case "17":
                this.setState({ roman_ranking_number: 'XVII' })
                break
              case "18":
                this.setState({ roman_ranking_number: 'XVIII' })
                break
              case "19":
                this.setState({ roman_ranking_number: 'XIX' })
                break
              case "20":
                this.setState({ roman_ranking_number: 'XX' })
                break
              default:
                this.setState({ roman_ranking_number: '' })
                break
            }
          })
      })
  }

  render() {
    return (<div
      className="standing">
        <h3>
          №{ this.state.ranking? this.state.ranking.position : '0' }
        </h3>
        в класирането до { this.state.roman_ranking_number } кръг
    </div>)
  }
}
