const host = 'https://fcbotev.com:9443/api';
// const host = 'http://localhost:3000/api';

async function fetchAllNews() {
    return await fetch(host +'/news', {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json'
        }
    });
}

async function fetchAllAdminNews() {
    return await fetch(host +'/admin/news', {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json'
        }
    });
}

async function fetchSingleNews(id) {
    return await fetch(host +'/news/'+ id, {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json'
        }
    });
}

async function tryToLogin(payload) {
    return await fetch(host +'/admin/login', {
        method: 'POST',
        body: payload
    });
}

async function createNews(payload) {
    return await fetch(host +'/create', {
        method: 'POST',
        body: payload
    });
}

async function deleteNews(id) {
    return await fetch(host +'/delete/'+ id, {
        method: 'DELETE',
        headers: {
            'Content-Type': 'application/json'
        }
    });
}

async function fetchRanking() {
    return await fetch(host +'/ranking', {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json'
        }
    });
}

async function fetchRankingFull() {
    return await fetch(host +'/ranking/full', {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json'
        }
    });
}

async function fetchSchedule() {
    return await fetch(host +'/schedule', {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json'
        }
    });
}

async function fetchLastgame() {
    return await fetch(host +'/lastgame', {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json'
        }
    });
}

export {
    fetchAllNews, 
    fetchAllAdminNews,
    fetchSingleNews,
    tryToLogin,
    createNews,
    deleteNews,
    fetchRanking,
    fetchRankingFull,
    fetchSchedule,
    fetchLastgame
}