import React, { Component } from 'react'
import { fetchLastgame } from './../api/request'

export default class Lastgame extends Component {
  constructor(props) {
    super(props)

    this.state = {
        lastgame: '',
        lastgame_result: ''
    }
  }

  componentDidMount() {
    fetchLastgame()
      .then(response => response.json())
      .then(dataLastgame => {
          this.setState({
            lastgame: dataLastgame
          }, () => {

            if (dataLastgame.is_home_game === true) {
              if (dataLastgame.winner === "1") {
                this.setState({
                  lastgame_result: 'П'
                })
              } else if (dataLastgame.winner === "2") {
                this.setState({
                  lastgame_result: 'З'
                })
              } else {
                this.setState({
                  lastgame_result: 'Р'
                })
              }
            } else {
              if (dataLastgame.winner === "1") {
                this.setState({
                  lastgame_result: 'З'
                })
              } else if (dataLastgame.winner === "2") {
                this.setState({
                  lastgame_result: 'П'
                })
              } else {
                this.setState({
                  lastgame_result: 'Р'
                })
              }
            }

          })
      })
  }

  render() {
    let lastgameTitle
    if (this.state.lastgame_result === 'П') {
      lastgameTitle = 'Победа срещу '+ this.state.lastgame.vs_team
    } else if (this.state.lastgame_result === 'З') {
      lastgameTitle = 'Загуба от '+ this.state.lastgame.vs_team
    } else {
      lastgameTitle = 'Равен с '+ this.state.lastgame.vs_team
    }
    return (<div
      className="lastgame"
      title={lastgameTitle}>
        <h3>
          {this.state.lastgame_result}
        </h3>
        {this.state.lastgame_result === 'П' &&
          <div>победа в последната среща с </div>
        }
        {this.state.lastgame_result === 'З' &&
          <div>загуба в последната среща с </div>
        }
        {this.state.lastgame_result === 'Р' &&
          <div>равен в последната среща за </div>
        }
        {this.state.lastgame.game_score}
    </div>)
  }
}
