import React, { Component } from 'react'
import { Link } from 'react-router-dom'

import { fetchAllNews, fetchSingleNews } from './../api/request'

import RankingBox from './Ranking'

export default class News extends Component {
    constructor() {
        super()

        this.state = {
            news: [],
            singleNews: ''
        }

        this.handleSingleNews = this.handleSingleNews.bind(this)
        this.clearSingleNews = this.clearSingleNews.bind(this)
    }

    handleSingleNews(e) {
        fetchSingleNews(e.target.id)
        .then(response => response.json())
        .then(singleNews => {
            this.setState({
                singleNews: singleNews
            })
        })
    }

    clearSingleNews() {
        this.setState({
            singleNews: ''
        })
    }

    componentWillMount() {
        fetchAllNews()
        .then(response => response.json())
        .then(allNews => {
            this.setState({
                news: allNews
            })
        })
    }

    componentWillUnmount() {
        this.setState({
          news: []
        })
    }

    render() {
        let classNumber = 1;
        let allNewsBar = (<div>
            <div className="news">
                {this.state.news.map((item, i) => {
                    let backgroundOfItem = {
                        backgroundImage: 'url("https://s3.eu-central-1.amazonaws.com/fcbotev/images/'+ item.picture +'")',
                        backgroundSize: 'cover',
                        backgroundPosition: 'center center'
                    }
                    return (<div 
                        className={'news-'+ classNumber++} 
                        style={backgroundOfItem}
                        key={i}>
                            <div className="newsTitle">
                                <Link to={'/article/'+item._id}>
                                    {item.title}
                                </Link>
                            </div>
                </div>)
            })}
            <br />
            </div>
            <RankingBox />
        </div>)

        let singleNewsBar = (<div className="single-article">
            <h1>{this.state.singleNews.title}</h1>
            <img src={'https://s3.eu-central-1.amazonaws.com/fcbotev/images/'+ this.state.singleNews.picture} />
            <div className="content">
                {this.state.singleNews.content}
            </div>
            <p onClick={this.clearSingleNews}
                class="return-to-news">
                ← Върни се в новините
            </p>
            <p class="news-date">
                Статията е от {this.state.singleNews.created_at? this.state.singleNews.created_at.substr(-10) + ", " + this.state.singleNews.created_at.substr(0,5) : ''}
            </p>
        </div>)

        return (<div>
            {this.state.singleNews? singleNewsBar : allNewsBar}
        </div>)
    }
}