import React, { Component } from 'react'
import { fetchSchedule } from '../api/request'

import logo from './../images/logo.png';
import nologo from './../images/no-logo.png';

export default class Nextgame extends Component {
  constructor(props) {
    super(props)

    this.state = {
        schedule: '',
        schedule_month: ''
    }
  }

  componentDidMount() {
    fetchSchedule()
      .then(response => response.json())
      .then(dataSchedule => {
          this.setState({
              schedule: dataSchedule
          }, () => {
            switch(dataSchedule.date.substr(3,2)) {
              case "1":
                this.setState({ schedule_month: 'януари' })
                break
              case "2":
                this.setState({ schedule_month: 'февруари' })
                break
              case "3":
                this.setState({ schedule_month: 'март' })
                break
              case "4":
                this.setState({ schedule_month: 'април' })
                break
              case "5":
                this.setState({ schedule_month: 'май' })
                break
              case "6":
                this.setState({ schedule_month: 'юни' })
                break
              case "7":
                this.setState({ schedule_month: 'юли' })
                break
              case "8":
                this.setState({ schedule_month: 'август' })
                break
              case "9":
                this.setState({ schedule_month: 'септември' })
                break
              case "10":
                this.setState({ schedule_month: 'октомври' })
                break
              case "11":
                this.setState({ schedule_month: 'ноември' })
                break
              case "12":
                this.setState({ schedule_month: 'декември' })
                break
              default:
                this.setState({ schedule_month: '' })
                break
            }
          })
      })
  }

  render() {
    return (<div
      className="nextgame">
        <ul>
          <li>
            {this.state.schedule.home === 'ФК Ботев Дебелец'? <img src={logo} alt="ФК Ботев Дебелец лого"/> : <img src={nologo} alt="няма лого"/>}
            {this.state.schedule.home? this.state.schedule.home : ''}
          </li>
          <li>
            <a className="nextgame-time">
              {this.state.schedule.time? this.state.schedule.time : ''}
            </a>
            <a className="nextgame-date">
            {this.state.schedule.date? this.state.schedule.date.substr(0,2) : ''}
            {this.state.schedule_month? " " + this.state.schedule_month + " " : ''}
            {this.state.schedule.date? this.state.schedule.date.substr(6,4) : ''}
            </a>
            <a className="nextgame-league">
              ОФГ Юг - Велико Търново 2018/19
            </a>
          </li>
          <li>
            {this.state.schedule.away === 'ФК Ботев Дебелец'? <img src={logo} alt="ФК Ботев Дебелец лого"/> : <img src={nologo} alt="няма лого"/>}
            {this.state.schedule.away? this.state.schedule.away : ''}
          </li>
        </ul>
    </div>)
  }
}
