import React, { Component } from 'react';
import './App.css';

import { Helmet } from 'react-helmet'
import { Switch, Route, withRouter } from "react-router-dom"

import Header from './components/Header'
import NewsPage from './components/News'
import ArticlePage from './components/Article'
import TeamPage from './components/Team'
import SchedulePage from './components/Schedule'
import NotfoundPage from './components/Notfound'
import AdminPage from './components/Admin'

import Standing from './components/Standing'
import Nextgame from './components/Nextgame'
import Lastgame from './components/Lastgame'

import logo from './images/logo.png'

class App extends Component {
  render() {
    return (<div>
      <Helmet>
        <meta charSet="utf-8" />
        <meta name='author' content='Stoyan Zdravkov' />
        <meta name="keywords" content="дебелец, ботев дебелец, ботев, фк ботев, фк ботев дебелец, дебелец, футбол, футболен клуб, fc botev, botev debelec" />
        <meta name="description" content="Официален сайт на ФК Ботев Дебелец." />

        <title>ФК Ботев Дебелец | fcbotev.com</title>

        <meta name="apple-mobile-web-app-capable" content="yes" />
	      <meta name="apple-mobile-web-app-status-bar-style" content="white" />
	      <meta name="apple-mobile-web-app-title" content="ФК Ботев Дебелец | fcbotev.com" />

        <meta name="viewport" content="user-scalable=yes" />
        <meta name="viewport" content="initial-scale=1.0, maximum-scale=3.0" />

        <meta property="og:url" content="/" />
        <meta property="og:site_name" content="ФК Ботев Дебелец | fcbotev.com" />
        <meta property="og:image" content={logo} />

        <link rel='shortcut icon' href={logo} />
	      <link rel='apple-touch-icon' href={logo} />
        <link rel="icon" type="image/png" href={logo} sizes="16x16" />
      </Helmet>
      <Header location={this.props.location}/>
      <div className="container">
        <div className="subcontainer">
          <Standing />
          <Lastgame />
          <Nextgame />
          <Switch>
            <Route
              exact
              path="/"
              render={props => <NewsPage {...props} />}
            />
            <Route
              exact
              path="/team"
              render={props => <TeamPage {...props} />}
            />
            <Route
              exact
              path="/schedule"
              render={props => <SchedulePage {...props} />}
            />
            <Route
              path="/article/:id"
              render={props => (
                <ArticlePage {...props} />
              )}
            />
            <Route
              exact
              path="/admin"
              render={props => <AdminPage {...props} />}
            />
            <Route component={NotfoundPage} />
          </Switch>
          <a href="https://www.facebook.com/BotevDebelec/" className="fbBox" target="_blank">
            Последвай ФК Ботев Дебелец във Facebook
          </a>
        </div>
      </div>
    </div>
    );
  }
}

export default withRouter(App)
