import React, { Component } from 'react'
import MetaTags from 'react-meta-tags'
import { fetchSingleNews } from './../api/request'

export default class Article extends Component {
    constructor(props) {
        super(props)

        this.state = {
            singleNews: ''
        }
    }

    componentDidMount() {
        fetchSingleNews(this.props.match.params.id)
        .then(response => response.json())
        .then(singleNews => {
            this.setState({
                singleNews: singleNews
            })
        })
    }

    render() {
        return (<div className="single-article">
            <MetaTags>
                <title>{'ФК Ботев Дебелец - ' + this.state.singleNews.title}</title>
                <meta name="description" content="Официален сайт на ФК Ботев Дебелец." />
                <meta property="og:title" content={this.state.singleNews.title} />
                <meta property="og:image" content={'https://s3.eu-central-1.amazonaws.com/fcbotev/images/'+ this.state.singleNews.picture} />
          </MetaTags>
        <h1>{this.state.singleNews.title}</h1>
        <img src={'https://s3.eu-central-1.amazonaws.com/fcbotev/images/'+ this.state.singleNews.picture} />
        <div className="content">
            <center>
            <b>{this.state.singleNews.created_at? this.state.singleNews.created_at.split(':')[0] + ":" + this.state.singleNews.created_at.split(':')[1] + ", " + this.state.singleNews.created_at.split(" ")[1] + " " + this.state.singleNews.created_at.split(" ")[2] + " " + this.state.singleNews.created_at.split(" ")[3]: ''}</b>
            </center>
            <br />
            <div className="content-text">
                {this.state.singleNews.content}
            </div>
        </div>
    </div>)
    }
}