import React, { Component } from 'react'
import { fetchRankingFull } from './../api/request'

export default class Schedule extends Component {
    constructor(props) {
        super(props)

        this.state = {
            teams: []
        }
    }

    componentDidMount() {
        fetchRankingFull()
        .then(response => response.json())
        .then(dataRanking => {
            let allTeams = []
            for (let team of dataRanking.ranking) {
                allTeams.push({position: team['position'], name: team['name'], games: team['games_played'], points: team['points']})
            }
            this.setState({
                teams: allTeams
            })
        })
    }

    render () {
        return (<div>
            <h3 className="teamTitle">
                Класиране
            </h3>
            <div className="ranking">
            <table>
                <thead>
                    <tr>
                        <th>
                            №
                        </th>
                        <th>
                            Отбор
                        </th>
                        <th>
                            Изиграни
                        </th>    
                        <th>
                            Точки
                        </th>
                    </tr>
                </thead>
                <tbody>
                {this.state.teams.map((item, i) => {
                    return (<tr key={i} className={item.name.includes('Дебелец')? 'mark' : ''}>
                        <td>
                            {item.position}
                        </td>
                        <td>
                        {item.name.includes('Дебелец')? 'ФК Ботев Дебелец' : item.name}
                        </td>
                        <td>
                            {item.games}
                        </td>
                        <td>
                            {item.points}
                        </td>
                    </tr>)
                })}
                </tbody>
            </table>
        </div>
        </div>)
    }
}